<template>
	<v-form ref="form">
		<v-select
			v-model="selectedRegion"
			:items="regionsList"
			label=""
			item-text="name"
			dense
			outlined
			:rules="rules.selectedRegion"
			return-object
		/>

		<v-select
			v-model="selectedPortal"
			:items="portalsList"
			label=""
			item-text="name"
			dense
			outlined
			:rules="rules.selectedPortal"
			return-object
		/>

		<v-menu
			v-model="showDateMenu"
			:close-on-content-click="false"
			:nudge-right="40"
			transition="scale-transition"
			offset-y
			min-width="auto"
		>
			<template v-slot:activator="{ on, attrs }">
				<v-text-field
					v-model="formattedDate"
					label=""
					prepend-icon="mdi-calendar"
					readonly
					dense
					outlined
					v-bind="attrs"
					v-on="on"
				></v-text-field>
			</template>
			<v-date-picker v-model="selectedDate" range></v-date-picker>
		</v-menu>

		<v-btn
			color="primary"
			:disabled="formLoading"
			:loading="formLoading"
			@click="applyForm"
			> </v-btn
		>
	</v-form>
</template>

<script>
	import { mapState, mapActions } from "vuex";
	export default {
		name: "JobForm",
		data: () => ({
			selectedRegion: null,
			selectedPortal: null,
			selectedDate: null,
			showDateMenu: false,
			formLoading: false,

			rules: {
				selectedRegion: [(v) => !!v || "  ."],
				selectedPortal: [(v) => !!v || "  ."],
			},
		}),
		computed: {
			...mapState(["regionsList", "portalsList"]),
			formattedDate() {
				if (!this.selectedDate) return "";
				return this.selectedDate
					.map((date) => this.formatDate(date))
					.join(" - ");
			},
		},
		methods: {
			...mapActions(["createNewJob"]),
			formatDate(date) {
				const [year, month, day] = date.split("-");
				return `${day}.${month}.${year}`;
			},

			async applyForm() {
				try {
					this.formLoading = true;
					const isFormValid = this.$refs.form.validate();
					if (!isFormValid) {
						return;
					}

					this.createNewJob({
						selectedPortal: this.selectedPortal,
						selectedDate: this.selectedDate,
					});
				} finally {
					this.formLoading = false;
				}
			},
		},
	};
</script>

<style lang="scss" scoped>
</style>