<template>
	<div class="jobs-table">
		<h2>   </h2>
		<v-data-table
			@click:row="handleRowClick"
			hide-default-footer
			:loading="loadingList"
			loading-text="   ..."
			:headers="headers"
			:items="jobsList"
			:items-per-page="Infinity"
			:item-class="() => 'jobs-table__row'"
			locale="ru-RU"
		>
		</v-data-table>
	</div>
</template>

<script>
	import { mapState, mapActions } from "vuex";
	export default {
		name: "JobsTable",
		data: () => ({
			loadingList: true,
			headers: [
				{
					text: "",
					value: "portalName",
					sortable: true,
				},
				{
					text: " ",
					sortable: true,
					value: "dateCreated",
				},
				{ text: "", value: "dateStart", sortable: true },
				{ text: "", value: "dateEnd", sortable: true },
				{ text: "", value: "statusLabel", sortable: true },
			],
		}),
		computed: {
			...mapState(["jobsList"]),
		},
		methods: {
			...mapActions(["getJobsList"]),

			handleRowClick() {
				console.log("table row clicked");
			},
		},
		async created() {
			try {
				await this.getJobsList();
			} finally {
				this.loadingList = false;
			}
		},
	};
</script>

<style scoped lang="scss">
	.jobs-table {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
</style>